@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;700&display=swap');

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    font-family: 'Quicksand', sans-serif;
    line-height: 1.8;
}

a {
    text-decoration: none;
}

p {
    padding-bottom: 1em;
    text-align: justify;
}

h1 {
    font-weight: 700;
    font-size: 3em;
    text-align: center;
    margin: 1em auto 0.5em;
}

h2 {
    font-weight: 500;
    font-size: 2em;
    text-align: center;
}

h3 {
    font-weight: 500;
    font-size: 1.2em;
}
